<template>
  <div class="detail">
    <product-params :title="product.name"></product-params>
    <div class="wrapper container">
      <div class="swiper-box">
        <template>
          <swiper :options="swiperOptions">
            <swiper-slide><img src="/imgs/detail/phone-1.jpg" alt="slide-img"></swiper-slide>
            <swiper-slide><img src="/imgs/detail/phone-2.jpg" alt="slide-img"></swiper-slide>
            <swiper-slide><img src="/imgs/detail/phone-3.jpg" alt="slide-img"></swiper-slide>
            <swiper-slide><img src="/imgs/detail/phone-4.jpg" alt="slide-img"></swiper-slide>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
            <div class="swiper-pagination" slot="pagination"></div>
          </swiper>
        </template>
      </div>
      <div class="content">
        <h2>{{product.name}}</h2>
        <p class="subtitle">
          3200 / 4800 /  / 6.39 AMOLED<br>
          /4030mAh / 710 /  / NFC /  / Game Turbo2.0
        </p>
        <p class="shop-name" title="
91110108558521630L
336 9019
400-100-5678
20100825  20400824
etc"></p>
        <p class="price">{{product.price}} <span class="discount">{{product.price + 1000}} </span></p>
        <div class="line"></div>
        <div class="loca">
          <span class="loca-icon"><img src="/imgs/detail/icon-loc.png" alt="icon"></span>
          <div class="loca-box">
            <span></span><span></span><span></span><span></span><span class="modify" @click="showModal = true"></span>
          </div>
          <p class="status"></p>
        </div>
        <h3></h3>
        <ul class="selection">
          <li :class="{active: version === 0}" @click="version = 0">{{verChoice[0]}}</li>
          <li :class="{active: version === 1}" @click="version = 1">{{verChoice[1]}}</li>
        </ul>
        <h3></h3>
        <ul class="selection">
          <li :class="{active: color === 0}" @click="color = 0">{{colChoice[0]}}</li>
          <li :class="{active: color === 1}" @click="color = 1">{{colChoice[1]}}</li>
          <li :class="{active: color === 2}" @click="color = 2">{{colChoice[2]}}</li>
        </ul>
        <div class="summary">
          <div class="box">
            <p class="final-choice">{{product.name}} {{verChoice[version]}} {{colChoice[color]}}</p>
            <p class="unit-price">{{product.price}}</p>
          </div>
          <p class="final-price">{{product.price}}</p>
        </div>
        <div class="btn-box btn-group">
          <button class="buy btn btn-huge" @click="addCart"></button>
          <button class=" like btn btn-huge btn-grey"></button>
        </div>
        <div class="after-sale-info">
          <span><i></i><em></em></span>
          <span><i></i><em></em></span>
          <span><i></i><em>7</em></span>
          <span><i></i><em></em></span>
          <span><i></i><em></em></span>
          <span><i></i><em></em></span>
          <span><i></i><em>7</em></span>
        </div>
      </div>
    </div>
    <div class="price-info">
      <div class="container">
        <h3></h3>
        <div class="price-des">
          <img src="/imgs/detail/item-price.jpeg" alt="price-info">
        </div>
      </div>
    </div>
    <footer-service></footer-service>
    <modal
    sureText=""
    cancelText=""
    btnType="3"
    modalType="middle" 
    :showModal="showModal"
    @submit="gotoCart"
    @cancel="showModal=false">
      <template v-slot:body>
        <p></p>
      </template>
    </modal>
  </div>
</template>
<script>
  import ProductParams from './../components/ProductParam'
  import FooterService from './../components/FooterService'
  import Modal from './../components/Modal'
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
  import 'swiper/css/swiper.css'
  export default {
    name:'detail',
    components:{
      ProductParams,
      FooterService,
      Modal,
      Swiper,
      SwiperSlide
    },
    data(){
      return {
        swiperOptions: {
          effect: 'fade',
          loop: true,
          autoplay: {
            disableOnInteraction: false
          },
          pagination: {
            el: '.swiper-pagination'
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl:'.swiper-button-prev'
          }
        },
        product: {},
        version: 0,
        color: 0,
        verChoice: ['6GB+64GB', '8GB+128GB'],
        colChoice: ['', '', ''],
        showModal: false,
        error:''
      }
    },
    mounted(){
      this.getProductInfo();
    },
    methods:{
      getProductInfo(){
        let id = this.$route.params.id;
        this.axios.get('/products/' + id).then((res)=>{
          this.product = res;
        })
      },
      addCart(){
        this.axios.post('/carts', {
          productId: this.product.id,
          selected: true
        }).then((res)=>{
          this.$store.dispatch('saveCartCount', res.cartTotalQuantity);
          this.showModal = true;
        }).catch((res)=>{
          this.error = res;
        })
      },
      gotoCart(){
        this.$router.push('/cart');
      }
    }
  }
</script>
<style lang="scss">
  @import './../assets/scss/config';
  @import './../assets/scss/mixin';
  .detail{
    .wrapper{
      display: flex;
      justify-content: space-between;
      padding: 32px 0 10px;
      .swiper-box{
        width: 600px;
        .swiper-container{
          width: 560px;
          text-align: center;
          img{
            width: 100%;
          }
          .swiper-button-prev{
            left: 0;
          }
          .swiper-button-next{
            right: 0;
          }
          .swiper-button-prev, .swiper-button-next{
            width: 40px;
            height: 70px;
            top: 47%;
            color: #66666652;
            &:hover{
              color:$colorG;
              background-color: #66666652;
              border-radius: 4px;
            }
          }
        }
      }
      .content{
        width: 600px;
        h2{
          font-size: 24px;
          font-weight: 400;
        }
        .subtitle{
          font-size: 14px;
          color: $colorD;
          padding-top: 8px;
          line-height: 1.5;
        }
        .shop-name{
          color: $colorA;
          font-size: 14px;
          margin-top: 1em;
        }
        .price{
          color: $colorA;
          font-size: 18px;
          padding: 12px 0 10px;
          .discount{
            color: $colorD;
            font-size: 14px;
            text-decoration: line-through;
            margin-left: 6px;
          }
        }
        .line{
          height: 12px;
          border-bottom: 1px solid $colorH;
        }
        .loca{
          margin: 20px 0;
          background-color: #fafafa;
          border: 1px solid #e0e0e0;
          padding: 30px 50px;
          font-size: 14px;
          line-height: 1.5;
          position: relative;
          .loca-icon{
            display: block;
            position: absolute;
            top: 32px;
            left: 24px;
            width: 16px;
            height: 16px;
            img{
              width: 74%;
              height: 100%;
            }
          }
          .loca-box{
            span{
              margin-right: 14px;
            }
            .modify{
              color: $colorA;
              cursor: pointer;
            }
          }
          .status{
            color: $colorA;
          }
        }
        h3{
          font-size: 18px;
          font-weight: 400;
        }
        .selection{
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          margin-bottom: 30px;
          li{
            width: 292px;
            height: 42px;
            line-height: 42px;
            text-align: center;
            font-size: 16px;
            color: $colorC;
            border: 1px solid #e0e0e0;
            margin-top: 10px;
            cursor: pointer;
            &:hover{
              color: $colorA;
              border-color: $colorA;
            }
          }
          .active{
            color: $colorA;
            border-color: $colorA;
          }
        }
        .summary{
          background-color: #f9f9f9;
          margin-bottom: 30px;
          padding: 30px;
          color: $colorC;
          .box{
            display: flex;
            justify-content: space-between;
            p{
              font-size: 14px;
              line-height: 1.5;
            }
          }
          .final-price{
            margin-top: 20px;
            font-size: 24px;
            color: $colorA;
          }
          }
        .btn-box{
          margin: 10px 0 20px;
          .like{
            width: 140px;
          }
        }
        .after-sale-info{
          display: flex;
          flex-wrap: wrap;
          span{
            font-size: 14px;
            color: $colorD;
            margin-right: 15px;
            margin-bottom: 20px;
            display: flex;
            i{
              width: 16px;
              height: 16px;
              text-indent: 2px;
              line-height: 22px;
              border: 1.5px solid $colorD;
              border-radius: 50%;
            }
            em{
              font-style: normal;
              margin-left: 3px;
              line-height: 19px;
            }
          }
        }
      }
    }
    .price-info{
      height: 316px;
      background-color: #f5f5f5;
      h3{
        font-size: 22px;
        font-weight: 400;
        padding: 1em 0;
      }
    }
  }
</style>